html {
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
  font-size: 16px; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  font: inherit;
  text-decoration: none;
  color: inherit; }
  a:hover {
    opacity: 0.6; }
  a:active, a:focus {
    opacity: 0.3; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.page-body {
  font-family: "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  transition: 0.5s;
  overflow-x: hidden;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: #f7eccf; }

.page-body__inner {
  margin: 0 auto;
  padding: 0 40px; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.desc {
  width: 700px; }

textarea {
  font-family: "Courier New", monospace;
  font-size: 1.5rem;
  width: 700px;
  outline: none;
  padding: 2rem;
  border-radius: 5px;
  resize: none;
  background-color: #ebe2d6; }

.keys-wrapper {
  display: flex;
  flex-direction: column;
  width: 700px;
  border-radius: 5px;
  margin-bottom: 40px; }

.row {
  display: flex; }

.key {
  flex: 0 1 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 35px;
  margin: 4px;
  white-space: nowrap;
  overflow: hidden;
  font-family: "Arial", sans-serif;
  font-size: 1rem;
  text-align: center;
  user-select: none;
  border-radius: 3px;
  box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.5);
  transition: 0.4s ease-in;
  cursor: pointer; }

.key.pressed {
  box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.5) inset;
  background-color: #b7b305; }

.wide {
  flex: 2 0 70px; }

.row:last-child .key:last-child {
  margin-right: 75px; }

.first-symbol {
  vertical-align: 50%;
  margin-right: 10px; }

.second-symbol {
  vertical-align: -50%; }

.key.caps-lock {
  background-color: #4b8f9c; }

.caps-lock.pressed {
  background-color: #4b8f9c; }

.lowercase {
  text-transform: lowercase; }
